.table_music {
  max-width: 1200px;
  margin: auto;
}
caption {
    font-weight: bold;
    font-size: 24px;
    text-align: center;
    margin: 10px 0 5px 0;
    color: white;
    font-family: "'Mon Amour Two Medium', arial";
  }
thead {
    text-align: center;
    font-weight: bold;
    color: white;
    /* border: 2px solid rgb(245, 242, 242); */
    background-color: #2154d3; 
  } 

tbody/* , tr, td */ {
    border-collapse: collapse;
    /* border: 1px solid rgb(238, 235, 235); */
    text-align: center;
   /*  color:#0000ff 0%; */
    background-color: #c4d0ee; 
  }
table {
    border-collapse: collapse;
    width: 100%;
     }
tr, td {
    /* color: black; */
    border: 2px solid white;
    height: 30px;
    font-family: "Comic Sans MS", cursive, sans-serif;
    font-style: italic;
    font-size: 18px;
  }
    
table td:first-child {
    text-align: center;
    width: 25px;
  }
tbody td:nth-child(2) {
    text-align: left;
  } 
tbody td:nth-child(3) {
    width: 100px;
  } 
tbody td:nth-child(4) {
    width: 100px;
  } 
  
a:hover {
  color: rgb(4, 4, 168);
  transition-duration: .3s;  
}
td > a {
  text-decoration: none;
}
  